@use "sass:map";
.second-box {
  padding: 0;
  margin-top: 1.25em;
  margin-bottom: 1.25em;
}
.column-col {
  &:first-child {
    padding-left: 0;
  }
  &:nth-child(2) {
    padding-right: 0.625em;
    padding-left: 0.625em;
  }
  &:last-child {
    padding-right: 0;
  }
}

.image-three--columns {
  max-width: 100%;
  border-radius: 0.5em 0.5em 0 0;
}
.box-text {
  height: 100%;
  font-family: $openSans-font;
  background-color: map.get($colors, box-background);
  border-radius: 0 0 0.5em 0.5em;
  box-shadow: 0 0.1875em 0 0 rgb(0 0 0 / 5%);
  padding: 3.5em 2.5em 2em;
  h2 {
    font-size: 1.4em;
    font-weight: 800;
    line-height: 1em;
    margin-bottom: 0.25em;
  }
  p {
    color: map.get($colors,navigation-text);
    font-size: 1em;
    font-weight: 300;
    line-height: 2.3em;
  }
  p:nth-child(2) {
    margin-bottom: 2em;
  }
}